import React from 'react';


const ScoreVisual = ({ score }) => (
  <div className="score-visual">
    <img src="/karbon-icon.webp" alt=""/>
    <span className={score > 66 ? "text-success" : (score > 33 ? "text-warning" : "text-danger")}>
      <big>{score}</big>/100
    </span>
  </div>
);


export { ScoreVisual };
